body {
    padding: 0;
    margin: 0;
    /* background-color: #f1f1f1; */
    font-family: 'Hind', sans-serif;
}

mark {
    font-weight: bold;
    font-style: unset;
    background-color: rgba(216, 216, 216, 0.75);
    padding: 0.2;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #5468ff;
    box-shadow: 0px 3px 16px -1px rgba(181, 181, 181, 1);
    position: fixed;
    width: 100%;
    z-index: 99;
    color: white;
}

h1 {
    font-family: 'Hind', sans-serif;
    font-weight: 400;
    font-size: 32px;
    margin-bottom: 16px;
}

nav ul {
    width: 40%;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 0 40px;
}

.left-part {
    margin-top: 79px;
    width: 20%;
    display: flex;
    align-items: flex-start;
    justify-content: left;
    padding: 20px;
    /* background-color: #f1f1f1; */
}

.right-part {
    margin-top: 79px;
    width: 80%;
    padding: 20px;
}

#hits {
    margin-top: 20px;
}

.item {
    padding: 1rem;
}

.ais-Hits-item .price {
    width: 100%;
    color: black;
    height: 40px;
    background-color: #dadada;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    position: absolute;
    bottom: 0;
    margin: 0;
}

.item .add-to-cart {
    border-radius: 50px;
    background-color: #5468ff;
    color: white;
    text-align: center;
    width: 70%;
    padding: 5px;
    margin-top: -50px;
    box-shadow: 0px 0px 43px 0px rgba(173, 173, 173, 1);
    cursor: pointer;
}

#pagination {
    margin-top: 20px;
}

.ais-Pagination-item--selected .ais-Pagination-link {
    background-color: #5468ff;
    border-color: #4b5684;
}

.ais-Pagination-link {
    color: #5468ff;
}

#algolia-search-input {
    width: 320px;
    border-radius: 50px;
    border: 1px solid white;
    background-color: #5468ff;
    color: white;
}

#algolia-search-input::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

#algolia-search-input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: white;
}

#algolia-search-input::-ms-input-placeholder {
    /* Microsoft Edge */
    color: white;
}

.bar {
    background: white;
}

#algolia-mic {
    background-color: white;
    border: 1px solid white;
    margin-left: -46px;
    border-radius: 50px;
    color: #5468ff;
    font-size: 18px;
}


/*MOBILE*/
@media screen and (max-width: 656px) {
    .hide-mobile {
        display: none;
    }

    nav {
        height: 74px;
    }

    nav h1 {
        width: 40%;
    }

    nav ul {
        width: 60%;
    }

    #algolia-search-input {
        width: 190px;
        height: 33px;
        padding-left: 10px !important;
    }

    #algolia-mic {
        margin-left: -32px;
        margin-right: 12px;
        font-size: 15px;
        width: 33px;
        height: 33px;
    }

    .content {
        padding: 10px;
    }

    .left-part {
        display: none;
    }

    .right-part {
        width: 100%;
        padding: 0;
    }

    #pagination {
        margin-bottom: 15px;
    }

    .ais-Hits-item {
        width: calc(50% - 1rem);
    }

    #bars {
        display: none;
    }
}

html {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
    line-height: 1.5;
    box-sizing: border-box;
}

body {
    margin: 0;
    color: #21243d;
}

input {
    font-family: inherit;
}

.materialIcons {
    vertical-align: middle;
    position: relative;
    top: -0.05em;
    line-height: 1;
    font-size: 1em;
    font-family: Material Icons;
    font-weight: 400;
    font-style: normal;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-feature-settings: 'liga';
}

@media (max-width: 900px) {
    [data-layout='desktop'] {
        display: none;
    }
}
@media (min-width: 900px) {
    [data-layout='mobile'] {
        display: none;
    }
}

/* tab managment */
.demo:not(.media) .homeTab,
.demo.media.searching .homeTab,
.demo.media:not(.searching) .resultsTab {
    display: none;
}

.container {
    display: flex;
    margin: 0 auto;
    max-width: 1100px;
    padding: 2rem 1rem;
}

@media (max-width: 900px) {
    .container {
        padding: 4rem 2rem;
    }
    @media (max-width: 680px) {
        .container {
            padding-right: 0;
        }
    }
}

.header {
    position: relative;
    align-items: center;
    background: linear-gradient(
    to bottom,
    transparent 36%,
    rgba(0, 0, 0, 0.1) 100%
    );
    background-color: #0058a3;
    background-position: center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 100vh;
    padding: 0.5rem 1rem;
    padding-top: 20vh;
    text-align: center;
    z-index: 10;
}
.demo.no-theme .header {
    background: none;
    background-color: rgba(0, 0, 0, 0);
    min-height: 15.5rem;
}

.header-logo {
    padding-bottom: 1rem;
    margin: 0;
}

.header-logo > img {
    height: 4rem;
}

@media (max-width: 1600px) {
    .header {
        padding-top: 10vh;
    }
}

@media (max-width: 900px) {
    .header {
        cursor: pointer;
        background-position: bottom;
        background-size: cover;
        min-height: 10.75rem;
        position: relative;
        transition: transform 0.2s ease-out;
    }

    .header-logo {
        left: 50%;
        pointer-events: none;
        position: absolute;
        top: 1.5rem;
        transform: translateX(-50%);
    }
    .demo.no-theme .header-logo,
    .demo.no-theme .searchWidget {
        position: relative;
        top: 0;
        left: 0;
        transform: none;
    }
}

.filtering .header {
    cursor: pointer;
}

.container-wrapper {
    flex-basis: 33%;
}
@media (max-width: 900px) {
    demo:not(.filtering) .container-wrapper {
        display: none;
    }
}
.demo.filters-right .container-wrapper {
    order: 1;
}

.main-wrapper {
    flex-basis: 67%;
}
@media (max-width: 900px) {
    .main-wrapper {
        flex-basis: 100%;
    }
}

.container-filters {
    flex: 1;
    margin-right: 3.75rem;
}
.demo.filters-right .container-filters {
    margin-right: 0;
}
.demo.filters-right:not(.filtering) .container-filters {
    margin-left: 3.75rem;
}
.demo.filters-right .container-filters {
    margin-right: 0;
}

@media (max-width: 900px) {
    .filtering .container-filters {
        position: absolute;
        transform: translateY(4rem);
        z-index: 40;
    }
    .no-theme.filtering .container-filters {
        box-shadow: 0px 0px 4px -1px rgba(17, 20, 50, 0.16),
        0px 2px 16px 0px rgba(35, 37, 51, 0.1);
    }
    .container-filters {
        background: #fff;
        border-radius: 16px;
        left: 0;
        max-width: none;
        padding-bottom: 4rem;
        position: fixed;
        top: 0;
        transform: translateY(120vh);
        transition: transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
        width: 100%;
        will-change: transform;
        z-index: 1;
    }
    .container-filters .container-body,
    .container-filters .container-header {
        padding-top: 2rem;
        padding-left: 2rem;
        padding-right: 2rem;
    }

    .container-filters-button {
        display: flex;
        justify-content: center;
    }

    .filters-button {
        align-items: center;
        border-style: none;
        border-radius: 0.5rem;
        bottom: 2rem;
        cursor: pointer;
        display: flex;
        font: inherit;
        font-size: 0.875rem;
        font-weight: 700;
        justify-content: center;
        min-height: 40px;
        min-width: 112px;
        position: fixed;
    }

    .filtering .filters-button,
    .container-filters-footer {
        display: none;
    }

    .filtering .container-filters-footer {
        background-color: #fff;
        border-top: 1px solid #ebecf3;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        /* margin: 0 -2rem; */
        padding: 1rem;
        position: fixed;
        width: 100%;
        z-index: 41;
    }

    .container-filters-footer-button-wrapper {
        width: calc(50% - 0.5rem);
    }
    .container-filters-footer-button-wrapper .ais-ClearRefinements {
        margin-top: 0;
        margin-left: 1rem;
    }

    .container-filters-footer .ais-ClearRefinements-button,
    .container-filters-footer .button {
        background-color: rgba(65, 66, 71, 0.08);
        border: none;
        border-radius: 8px;
        cursor: pointer;
        display: flex;
        font: inherit;
        font-size: 0.75rem;
        font-weight: 700;
        justify-content: center;
        padding: 12px;
        text-align: center;
        width: 100%;
    }

    .container-filters-footer .button-primary {
        background-color: rgb(84, 101, 252);
        color: #fff;
    }
}

.container-body {
    flex-basis: 21.625rem;
